* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    color: #333;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

:root {
    --good-blue: #6c6cff;
}

body,
html {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

#root {
    height: 100%;
}

button {
    border: 1px solid #ccc;
    border-radius: 0.25em;
    background: none;
    cursor: pointer;
    padding: 0.5em 1em;
}

button:disabled {
    background-color: #ddd !important;
    cursor: default;
}

ul, ol {
    padding: 0 2rem;
}

textarea {
    height: 10em;
    resize: vertical;
}

.card {
    padding: 1rem 2rem;
    border: none;
}

.hint-text {
    color: var(--good-blue);
    font-weight: bold;
    font-style: italic;
}

.main-outline {
    height: 100%;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
}

.header-bar {
    border: 1px solid #dddd;
    border-style: none none solid none;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
}

.header-bar > .title {
    color: #3f3fbb;
}

.sign-out-btn {
    position: fixed;
    top: 1rem;
    right: 1rem;
}

.sign-out-btn:hover {
    background-color: rgb(155, 42, 42);
    color: #fff;
}

.main-nav {
    margin: 0 auto;
}

.main-nav {
    display: flex;
    gap: 2rem;
    padding: 1rem;
}

.phaseTabs {
    background-color: var(--good-blue);
    font-weight: bold;
    font-size: 2em;
    width: 2em;
    height: 2em;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
}

.phaseTabs.selected-btn {
    outline: 2px solid #333;
    /* border-radius: 0.5em; */
}

.controller-nav {
    margin-top: auto;
    width: 100%;
    display: flex;
    padding: 1rem;
}

.controller-nav .prev {
    margin-right: auto;
}

.controller-nav .next {
    margin-left: auto;
}

.controller-nav .nav-btn {
    background-color: var(--good-blue);
    font-weight: bold;
    font-style: italic;
    font-size: 1.2em;
}

.main-view {
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    align-items: center;
    height: 100%;
    overflow: hidden;
}

.main-view .card {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    border: none;
    flex-grow: 0;
    max-height: 100%;
    overflow: auto;
}

.card .card-body {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    flex-grow: 0;
}

.two-div-outline-container {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 1rem;
    font-size: 1.3rem;
    gap: 1rem;
    height: 100%;
    overflow: hidden;
}


.two-div-outline-container > .outline {
    border: 1px solid #333;
    border-radius: 1rem;
    padding: 1rem;
    flex: 0 0 35%;
    overflow: auto;
}

.two-div-outline-container > .outline li {
    text-decoration: underline;
    margin-bottom: 1rem;
    cursor: pointer;
}

.two-div-outline-container > .outline li.disabled {
    text-decoration: none;
    color: #bbb;
    user-select: none;
    pointer-events: none;
    cursor: default;
}

.two-div-outline-container > .outline li.active::before {
    content: "~";
    display: inline-block;
    margin-right: 0.5rem;
}

.two-div-outline-container > .outline-rhs {
    padding: 1rem;
    height: 100%;
    overflow-y: auto;
}

.slideshow a {
    color: var(--good-blue) !important;
    font-style: italic;
    cursor: pointer;
}

.form-slides {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    overflow: hidden;
    text-align: center;
    font-size: 1.2rem;
}

.form-slides > .expectation {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    width: 65%;
    align-self: center;
}

.form-slides > .expectation > .expectation-benchmarks {
    width: 100%;
}

.form-slides > .expectation > .expectation-benchmarks > .benchmark {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;

}

.benchmark textarea {
    width: 100%;
    height: 5rem;
}

.benchmark .save-btn {
    margin-left: auto;
}

.benchmark .save-btn:hover {
    background-color: #7b7bc5;
}

.feedback-prompt {
    display: flex;
    /* align-items: center; */
    justify-content: center;
    height: 100%;
}

.feedback-prompt .self-rating-prompt {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    align-items: center;
    /* justify-content: center; */
    padding: 3rem;
    min-width: 30%;
    height: 100%;
    overflow-y: auto;
    position: relative;
}

/* .feedback-prompt .self-rating-prompt.rated::after {
    content: "";
    border: 1px solid #999;
    border-style: none solid none none;
    height: 100%;
    position: absolute;
    right: 0;
} */

.feedback-prompt .self-rating-prompt ol {
    text-align: left;
}

.feedback-prompt .self-rating-prompt ol li {
    margin: 0.5rem 0;
    padding: 0 1rem;
}

.feedback-prompt .rating-btns {
    display: flex;
    gap: 1rem;
}

.feedback-prompt .rating-btns button:hover {
    background-color: #ddd;
}

.feedback-prompt .feedback {
    max-height: 100%;
    width: 35%;
    text-align: left;
    padding: 3rem 5rem;
    overflow-y: auto;
}

.feedback li,p {
    margin: 0.25rem 0;
}

.tabbed {
    padding-left: 1rem;
}

.feedback-form {
    width: 35%;
    height: 80%;
    border: 1px solid #999;
    border-style: none none none solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.feedback-form textarea {
    width: 75%;
    height: 5em;
    padding: 2px 3px;
}